.m-1{
    margin: 4px;
}

.m-2{
    margin: 8px;
}


.ml-1{
    margin-left: 4px;
}

.ml-2{
    margin-left: 8px;
}

.mr-1{
    margin-right: 4px;
}

.mr-2{
    margin-right: 8px;
}
.checkboxGroup .form-check{
    display: inline-block !important;
    padding-right: 10px !important;
    clear: both;
    min-width: 100px !important;
}
.checkboxGroup .form-label{
    padding-bottom: 5px !important;
}
.filtersSection .box {
    border: none !important;
    border-bottom :solid 1px #eee !important;
    box-shadow: none !important;
}
.filtersSection .box-header{
    display: none !important;
}

a.logo{
    background-size: contain !important;
    background-repeat: no-repeat !important;
    background-color: #fff !important;
    background-position: center !important;
}

.main-header .sidebar-toggle{
    border-left: solid 1px #eee !important;
}

.linebreak{
    border-top: solid 1px #fff !important;
}

.main-sidebar{
    padding-top: 0 !important;
}

.skin-black .main-header{
    box-shadow: none !important;
}

.skin-black .main-header > .logo{
    display: none !important;
    opacity: 0 !important;
    visibility: hidden !important;
}

.notification-alert-box .alert{
    border-radius: 0 !important;
    display: flex !important;
    justify-content: space-between !important;
    align-items: center !important;
    border: none !important;
    box-shadow: none !important;
    z-index: 900 !important;
    position: relative !important;
    margin: 0 !important;
    border-bottom: solid 1px #fff !important; 
}

.notification-alert-box .alert a.visit{
    text-decoration: none !important;
    background-color: white !important;
    color: black !important;
    padding: 5px 20px;
    border-radius: 10px;
    font-weight: bold !important;
}

.notification-alert-box .alert h4{
    margin-bottom: 0 !important;
}
.notification-alert-box {
}

.skin-black .sidebar-menu > li.active > a{
    border-left-color: #555 !important;
}

.userLoggedInInfo{
    min-height: 100px;
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    border-top: solid 1px #fff !important;
    color: #fff !important;
    text-align: center !important;
    padding: 5px !important;
    margin-top: 20px !important;
}

.sidebar-menu, .main-sidebar .user-panel, .sidebar-menu > li.header{
    white-space: normal !important;
}

.sidebar-collapse .userLoggedInInfo{
    display: none !important;
}

.sidebar-collapse a.logo img{
    padding: 5px !important;
}

.login-box{
    margin-top: 0 !important;
    margin-bottom: 0 !important;
}
.login-box:first-child{
    margin-top: 50px !important;
}

.loginFooter{
    background-color: #eee !important;
    text-align: center !important;
    padding: 10px 0 !important;
    margin-top: 0 !important;
    border: solid 1px #eee !important;
}
.loginFooter h4{
    font-size: 12px !important;
    font-weight: bold !important;
    text-transform: uppercase !important;
    color: #000 !important;
}
.loginFooter .footerLogo{
    width: 100px !important;
    height: inherit !important;
}

.globalAlert .alert{
    border-radius: 0 !important;
    position: relative !important;
    width: 100% !important;
    z-index: 9999 !important;
}
.globalAlert .alert button{
    color: #fff !important;
    opacity: 1 !important;
}

.deviceStatusSection{
    position: relative !important;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center !important;
    justify-content: space-between;
}

.deviceStatusSection .btn{
    border-radius: 50%;
    width: 25px !important;
    height: 25px !important;
    border: none !important;
    padding: 0 !important;
    position: absolute !important;
    top: 0;
    left: 100px !important;
}

.fixed-logo-area{
    min-height: 120px !important;
    display: flex !important;
    justify-content: space-between !important;
    align-items: center !important;
    width: 100% !important;
    padding: 10px 20px !important;
}
.fixed-logo-area img.leftLogo,.fixed-logo-area img.rightLogo {
    width: 120px !important;
    height: inherit !important;
}

.rdrDateDisplayWrapper{
    display: none !important;
}

.datePicketContainer{
    position: absolute !important;
    z-index: 999 !important;
    background:#fff !important;
    box-shadow: -3px 10px 10px #eee !important;
    border: solid 1px #eee !important;
    padding-bottom: 20px !important;
    display: flex !important;
    flex-direction: column !important;
    justify-content: center !important;
    align-items: center !important;
}
.datePicketContainer button.filterByDates{
    width: 200px !important;
}
.datePicketContainer.hidden{
    display: none !important;
}